import React, { Component } from "react";

import "./_apps.scss";

import PopupApps, { popupApps } from "./PopupApps";

class Apps extends Component {
  render() {
    return (
      <div
        className="btn-apps"
        onMouseEnter={() => popupApps("40px", "100px", "showApps")}
        onMouseLeave={() => popupApps("30px", "55px", "hiddenApps")}
      >
        Apps
        <PopupApps />
      </div>
    );
  }
}

export default Apps;
