import React, { Component } from "react";
import $ from "jquery";

import "./_favorites_apps.scss";

import { data } from "../footer/Data";

export const popupFavs = (height, name) => {
  $(".btn-favorites").css({ height: height });
  $(".popup-favorites").css({ animation: `0.3s ${name} both` });
};

class PopupFav extends Component {
  render() {
    return (
      <div className="popup-favorites">
        <ul>
          {data.map((index, key) => {
            return (
              <a href={index.link} key={key}>
                <li>
                  <img src={index.icon} alt={index.icon} width="20" />
                  {index.text}
                </li>
              </a>
            );
          })}
        </ul>
      </div>
    );
  }
}

export default PopupFav;
